import { Card } from "@/components/ui/card";
import type { StateRegulation } from "@/data/states";
import { formatCurrency } from "@/lib/utils";

type QuickFactsLocale = "en" | "es";

export function StateQuickFacts({
  state,
  locale = "en",
}: {
  state: StateRegulation;
  locale?: QuickFactsLocale;
}) {
  const complianceSummaryEn = state.complianceSummary;
  const complianceSummaryEs = state.complianceSummaryEs ?? complianceSummaryEn;

  const copy = locale === "es"
    ? {
        kicker: "Datos clave",
        heading: `Panorama crediticio en ${state.name}`,
        paydayAllowed: "Payday permitido",
        paydayMax: "Monto máximo payday",
        installmentMax: "Monto máximo a plazos",
        aprRange: "Rango APR",
        termWindow: "Plazo típico",
        yes: "Sí",
        no: "No",
        daysSuffix: "días",
        na: "No aplica",
        disclaimer:
          `${complianceSummaryEs} Verifica con el regulador estatal antes de firmar cualquier contrato de préstamo. Esta información es solo educativa.`,
      }
    : {
        kicker: "Quick facts",
        heading: `Lending snapshot for ${state.name}`,
        paydayAllowed: "Payday allowed",
        paydayMax: "Payday max",
        installmentMax: "Installment max",
        aprRange: "APR range",
        termWindow: "Term window",
        yes: "Yes",
        no: "No",
        daysSuffix: "days",
        na: "N/A",
        disclaimer:
          `${complianceSummaryEn} Always verify with state regulators before signing any loan contract. This information is for education only.`,
      };

  const formattedTermWindow = `${state.minTermDays} – ${state.maxTermDays} ${copy.daysSuffix}`;

  return (
    <Card className="space-y-5">
      <header className="space-y-2">
        <p className="text-xs uppercase tracking-[0.3em] text-slate-400">{copy.kicker}</p>
        <h2 className="text-2xl font-semibold text-slate-900">{copy.heading}</h2>
      </header>
      <dl className="grid gap-3 text-sm text-slate-600">
        <div className="flex items-center justify-between rounded-2xl bg-white/70 px-4 py-3 shadow-inner">
          <dt className="font-medium text-slate-500">{copy.paydayAllowed}</dt>
          <dd className="font-semibold text-slate-900">
            {state.paydayAllowed ? copy.yes : copy.no}
          </dd>
        </div>
        <div className="flex items-center justify-between rounded-2xl bg-white/70 px-4 py-3 shadow-inner">
          <dt className="font-medium text-slate-500">{copy.paydayMax}</dt>
          <dd className="font-semibold text-slate-900">
            {state.paydayAllowed ? formatCurrency(state.paydayMaxAmount) : copy.na}
          </dd>
        </div>
        <div className="flex items-center justify-between rounded-2xl bg-white/70 px-4 py-3 shadow-inner">
          <dt className="font-medium text-slate-500">{copy.installmentMax}</dt>
          <dd className="font-semibold text-slate-900">
            {formatCurrency(state.installmentMaxAmount)}
          </dd>
        </div>
        <div className="flex items-center justify-between rounded-2xl bg-white/70 px-4 py-3 shadow-inner">
          <dt className="font-medium text-slate-500">{copy.aprRange}</dt>
          <dd className="font-semibold text-slate-900">
            {state.typicalAprRange[0]}% – {state.typicalAprRange[1]}%
          </dd>
        </div>
        <div className="flex items-center justify-between rounded-2xl bg-white/70 px-4 py-3 shadow-inner">
          <dt className="font-medium text-slate-500">{copy.termWindow}</dt>
          <dd className="font-semibold text-slate-900">{formattedTermWindow}</dd>
        </div>
      </dl>
      <p className="text-xs text-slate-500 leading-6">{copy.disclaimer}</p>
    </Card>
  );
}
